<template>
  <div id="container">
    <div id="terms">
      <div class="terms-pic">
        <img src="../files/terms.svg" alt="">
      </div>
      <div class="terms-content">
        <p>Однозначно, многие известные личности формируют глобальную экономическую сеть и при этом —  объявлены нарушающим.</p>
        <p>Однозначно, многие известные личности формируют глобальную экономическую сеть и при этом —  объявлены нарушающим.
          <br>Однозначно, многие известные личности формируют глобальную экономическую сеть и при этом —  объявлены нарушающим.</p>

        <p>Однозначно, многие известные личности формируют глобальную экономическую сеть и при этом —  объявлены нарушающим.</p>
        <p>Однозначно, многие известные личности формируют глобальную экономическую сеть и при этом —  объявлены нарушающим.</p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Terms",
  data: () => ({
    user_id: 0,
  }),
  mounted() {
    const telegram = window.Telegram.WebApp;
    const telegramData = telegram.initDataUnsafe;
    const mainButton = telegram.MainButton;
    const backButton = telegram.BackButton;


    this.user_id = telegramData.user.id

    axios
        .get('https://back.sombra.uz/api/user/' + this.user_id)
        .then(response => {
          if (!response.data.data.agreed) {
            mainButton.show()
            mainButton.setParams({
              'text': "Согласен",
              'color': "#f2c058",
              'text_color': "#FFF"
            })
            mainButton.onClick(() => {
              axios
                  .post('https://back.sombra.uz/api/user/' + this.user_id, {
                    agreed: 1,
                  })
                  .then(response => {
                    this.$router.push('/')
                  })
                  .catch(error => {
                    telegram.showPopup(error)
                  })
            });
          } else {
            backButton.show()
            backButton.onClick(() => {
              this.$router.push('/')
            });
          }
        })
        .catch(error => console.log(error));
  }
}
</script>

